<template>
  <div class="container">
    <div class="col">
      <button class="btn btn-primary" style="margin: 5px;" @click="section = true">File a Complaint</button>
      <button class="btn btn-success" style="margin: 5px;" @click="clickedSignUp">Sign Up</button>
    </div>
    <br>
    <form v-if="section">
      <div class="row">
        <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3">
          <h1>File a Complaint</h1>
          <br>
          <div class="form-group">
            <label for="email">Mail</label>
            <input type="email" id="email" class="form-control" v-model="userData.email">
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <input type="password" id="password" class="form-control" v-model="userData.password">
          </div>
          <div class="form-group">
            <label for="age">Age</label>
            <input type="number" id="age" class="form-control" v-model="userData.age">
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3 form-group">
          <label for="message">Message</label>
          <br>
          <!-- Interpolation between <textarea>{{ test }}</textarea> doesn't work!-->
          <textarea id="message" rows="5" class="form-control" v-model="message"></textarea>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3">
          <div class="form-group">
            <label for="sendmail" style="margin-right: 20px;">
              <input type="checkbox" id="sendmail" value="SendMail" v-model="sendMail"> Send Mail
            </label>
            <label for="sendInfomail">
              <input type="checkbox" id="sendInfomail" value="SendInfoMail" v-model="sendMail"> Send Infomail
            </label>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3 form-group">
          <label for="male" style="margin-right: 20px;">
            <input type="radio" id="male" value="Male" v-model="gender"> Male
          </label>
          <label for="female">
            <input type="radio" id="female" value="Female" v-model="gender"> Female
          </label>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3 from-group">
          <label for="priority">Priority</label>
          <select id="priority" class="form-control" v-model="selectedPriority">
            <option
              v-for="(priority, index) in priorities"
              :key="index"
              :selected="priority == 'Medium'"
            >{{ priority }}</option>
          </select>
        </div>
      </div>
      <SwitchApp v-model="dataSwitch"></SwitchApp>
      <hr>
      <div class="row">
        <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3">
          <!-- .prevent doesn't send data to server, instead to a self configured method e.g. -->
          <button class="btn btn-primary" @click.prevent="submitted">Submit</button>
        </div>
      </div>
    </form>
    <hr>
    <div class="row" v-if="isSubmitted && section">
      <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3">
        <div class="card">
          <div class="card-header">
            <h4>Your Data</h4>
          </div>
          <div class="card-body">
            <p>Mail: {{ userData.email }}</p>
            <p>Password: {{ userData.password }}</p>
            <p>Age: {{ userData.age }}</p>
            <p style="white-space: pre">Message: {{ message }}</p>
            <p>
              <strong>Send Mail?</strong>
            </p>
            <ul>
              <li v-for="(item, index) in sendMail" :key="index">{{ item }}</li>
            </ul>
            <p>Gender: {{ gender }}</p>
            <p>Priority: {{ selectedPriority }}</p>
            <p>Switched: {{ dataSwitch }}</p>
          </div>
        </div>
      </div>
    </div>
    <assignment9  v-if="!section"></assignment9>
  </div>
</template>

<script>
import Switch from "./components/Switch.vue";
import Assignment9 from './components/Assignment9.vue';

export default {
  components: {
    SwitchApp: Switch,
    Assignment9
  },
  data() {
    return {
      userData: {
        email: "",
        password: "",
        age: 0
      },
      message: "Enter your message",
      sendMail: [],
      gender: "Female",
      priorities: ["High", "Medium", "Low"],
      selectedPriority: "High",
      dataSwitch: true,
      isSubmitted: false,
      section: true
    };
  },
  methods: {
    submitted() {
      this.isSubmitted = true;
    },
    clickedSignUp() {
      this.isSubmitted = false;
      this.section = false;
    }
  }
};
</script>

