<template>
  <div class="container">
    <form action class="form-group" v-if="!submitData">
      <div class="row">
        <h1>Sign Up</h1>
      </div>
      <br>
      <div class="row">
        <label for="firstName">First Name</label>
        <input type="text" id="firstName" class="form-control" v-model="firstName">
        <label for="lastName">Last Name</label>
        <input type="text" id="lastName" class="form-control" v-model="lastName">
        <label for="email">Mail</label>
        <input type="email" id="email" class="form-control" v-model="email">
        <label for="password">Password</label>
        <input type="password" id="password" class="form-control" v-model="password">
      </div>
      <div class="row">
        <label for="storeData">
          Store Data?
          <div class="col">
            <input type="radio" value="Yes" id="yes" v-model="storeData"> Yes
          </div>
          <div class="col">
            <input type="radio" value="No" id="no" v-model="storeData"> No
          </div>
        </label>
      </div>
      <div class="row">
        <button class="btn btn-warning" @click.prevent="submitData = true">Submit</button>
      </div>
    </form>
    <div class="row" v-if="submitData">
      <div class="card">
        <div class="card-header">Sign Up Details</div>
        <div class="card-body">
          <p>Full Name: {{ fullName }}</p>
          <p>Email: {{ email }}</p>
          <p>Password: {{ password }}</p>
          <p>Store Data: {{ storeData }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      firstName: "",
      lastName: "",
      email: "",
      password: "",
      storeData: "Yes",
      submitData: false
    };
  },
  computed: {
    fullName() {
      return this.firstName + " " + this.lastName;
    }
  }
};
</script>

